  }

  #welcome {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow: hidden;
    padding: 0;
  }

  #welcome > * {
    width: 100%;
  }

  .description {
    margin-top: 2rem;
  }

  #services {
    padding: 1rem;
  }
  
  .cards-wrap {
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 1rem;
  }

  .cards-wrap > * {
    width: 100%;
    margin: 1rem;
  }
}
